import { Component, OnInit } from "@angular/core";
import { FormGroup } from "@angular/forms";
import { FieldConfig } from "../../field.interface";
@Component({
  selector: "app-multi-select",
//   template: `
// <mat-form-field class="demo-full-width margin-top" [formGroup]="group">
// <mat-select [placeholder]="field.label" [formControlName]="field.name">
// <mat-option *ngFor="let item of field.options" [value]="item">{{item}}</mat-option>
// </mat-select>
// </mat-form-field>
// `,
  template: `
  <mat-list-item class="primary-imenu-item" role="listitem">
    <mat-form-field class="select-form">
        <mat-select 
        placeholder="Years" 
        name="year" 
        class="filter-select" 
        [(ngModel)]="selectedYears" 
        [compareWith]="equals"
        multiple 
        #yearSelect="ngModel">
          <mat-option disabled="disabled" class="filter-option">
            <input type="checkbox"(click)="selectAll(checkAll.checked, yearSelect, years)" #checkAll>
            <!--<mat-option (click)="selectAll(yearSelect, years)" #checkAll> Select All-->
          </mat-option>
          <mat-option *ngFor="let year of years" [value]="year">
            {{year.viewValue}}
          </mat-option>
        </mat-select>
    </mat-form-field>
  </mat-list-item>
  `,
  styles: []
})
export class MultiSelectComponent implements OnInit {
  field: FieldConfig;
  group: FormGroup;
  years: any[];
  constructor() {}
  ngOnInit() {
    this.years = [
      {id: 1, viewValue: "2017"},
      {id: 2, viewValue: "2018"},
      {id: 3, viewValue: "2019"},
      {id: 4, viewValue: "2020"},
      {id: 5, viewValue: "2021"}
    ]
  }
}
